    .activitySubTitle{
      font-size: 15px!important;
    }
    .headerName{
      font-size: 15px!important;
    }
    .item-note{
      font-size: 15px!important;
    }
    .timeInfo{
      font-size: 14px!important;
    }
    .inputNumber{
      font-size: 15px!important;
    }
    .buttonGrayStyle{
      font-size: 22px!important;
    }
    .activityHead{
      font-size: 17px!important;
      font-weight: bold;
    }
  }
