import http from "../http";

export {
    getActivityListService,
    getActivityDetailInfoService,
    getEtcDetailService,
    getJoinInfoService,
    getCancelStateService,
    getReadyStateService,
    getScreeningsService
}

//获取活动列表
const getActivityListService = (params) => {
    return new Promise((resolve, reject) => {
        http.post('hisense-systemServer/app/owner/ownerActivitiesController/getActiMsg.do', params,true, "homeUrl", false).then(result => {
            //console.log(JSON.stringify(result));
            resolve(result);
        }).catch(error => {
            reject(error);
        })
    });
}

